import { GetComponentProps, Modal, useEditor, useEditorActions } from 'topwrite';
import { ReactComponent as Icon } from '../images/icon.svg';

export default function Tool({ Component }: GetComponentProps<'editor:tool'>) {
    const editor = useEditor();

    const handler = async () => {
        const result = await Modal.prompt<{ name: string, url: string, cover: string }[]>({
            title: '插入音乐',
            size: 'xl',
            schema: {
                type: 'array',
                minItems: 1,
                items: {
                    type: 'object',
                    properties: {
                        name: {
                            type: 'string',
                            title: '名称'
                        },
                        url: {
                            type: 'string',
                            title: 'URL地址'
                        },
                        cover: {
                            type: 'string',
                            title: '封面图片地址'
                        }
                    }
                }
            },
            uiSchema: {
                items: {
                    name: {
                        'ui:col': 3,
                        'ui:options': {
                            label: false,
                            placeholder: '名称'
                        }
                    },
                    url: {
                        'ui:col': 5,
                        'ui:options': {
                            label: false,
                            placeholder: 'URL地址'
                        }
                    },
                    cover: {
                        'ui:col': 4,
                        'ui:options': {
                            label: false,
                            placeholder: '封面图片地址'
                        }
                    }
                }
            }
        });

        if (result) {
            let lines = [];

            for (let audio of result) {
                if (audio.name && audio.url) {
                    let line = `${audio.name}|${audio.url}`;
                    if (audio.cover) {
                        line += `|${audio.cover}`;
                    }
                    lines.push(line);
                }
            }
            const { insertXBlock } = useEditorActions();

            insertXBlock(editor, {
                name: 'audio',
                value: lines.join('\n')
            });
        }
    };

    return <Component
        title={'插入音乐'}
        handler={handler}
    >
        <Icon />
    </Component>;
}
